/* Customize the nav-justified links to be fill the entire space of the .navbar */
.nav-justified[data-v-345c7af4] {
  /**/
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.nav-justified > li > a[data-v-345c7af4] {
  /*设置每个li 的 子a标签*/
  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 0;
  font-weight: bold;
  color: rgb(70, 172, 175);
  text-align: center;
  background-color: #e5e5e5; /* Old browsers */
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f5f5f5), to(#e5e5e5));
  background-image: linear-gradient(to bottom, #f5f5f5 0%, #e5e5e5 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f5f5f5', endColorstr='#e5e5e5',GradientType=0 ); /* IE6-9 */
  background-repeat: repeat-x; /* Repeat the gradient */
  border-bottom: 1px solid #d5d5d5;
}
.nav-justified > .active > a[data-v-345c7af4],
.nav-justified > .active > a[data-v-345c7af4]:hover,
.nav-justified > .active > a[data-v-345c7af4]:focus {
  background-color: #ddd;
  background-image: none;
  -webkit-box-shadow: inset 0 3px 7px rgba(0, 0, 0, 0.15);
  box-shadow: inset 0 3px 7px rgba(0, 0, 0, 0.15);
}
.nav-justified > li:first-child > a[data-v-345c7af4] {
  border-radius: 5px 5px 0 0;
}
.nav-justified > li:last-child > a[data-v-345c7af4] {
  border-bottom: 0;
  border-radius: 0 0 5px 5px;
}
@media (min-width: 768px) {
.nav-justified[data-v-345c7af4] {
    max-height: 52px;
}
.nav-justified > li > a[data-v-345c7af4] {
    border-right: 1px solid #d5d5d5;
    border-left: 1px solid #fff;
}
.nav-justified > li:first-child > a[data-v-345c7af4] {
    border-left: 0;
    border-radius: 5px 0 0 5px;
}
.nav-justified > li:last-child > a[data-v-345c7af4] {
    border-right: 0;
    border-radius: 0 5px 5px 0;
}
}
/* Responsive: Portrait tablets and up */
@media screen and (min-width: 768px) {
  /* Remove the padding we set earlier */
.masthead[data-v-345c7af4],
  .marketing[data-v-345c7af4],
  .footer[data-v-345c7af4] {
    padding-right: 0;
    padding-left: 0;
}
}

